// -----------------------------------------------------------------------------
// 		Fourth row : AVAILABLE COURSES
// -----------------------------------------------------------------------------

.node-school {
	.field-item {
		.course-wrap {
			background: #F0F0F0;
			padding-bottom: 0;

			.course {
				.section-head {

				}
				.section-body {
					.flex-items {
						margin-left: -15px;
						margin-right: -15px;

						@media #{$sm-min} {
							&.five-course-types,
							&.six-course-types {
								justify-content: center;
								.flex-item {
									flex: 0 0 33.33333333%;
									max-width: 33.33333333%;
								}
							}
						}

						.flex-item {
							display: flex;
							position: relative;
							text-align: center;
							padding: 5px 15px;
							flex: 0 0 100%;
							max-width: 100%;
							align-self: stretch;

							@media #{$sm-min} {
								flex: 1;
								max-width: none;
								padding: 0.75%;
							}

							.item-inner {
								position: relative;
								width: 100%;
								padding: 15px;
								padding-bottom: 70px;
								text-align: left;
								-webkit-box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.025);
								-moz-box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.025);
								box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.025);
								background-color: #F0F0F0;

								@media #{$sm-min} {
									background-color: #FFFFFF;
								}

								.icon-container {
									float: left;
									font-size: 36px;
									margin: 0 6px 0 0;
									color: #404040;

									@media #{$sm-min} {
										float: none;
										font-size: 48px;
									}
								}

								.text-container {
									overflow: hidden;
									padding-top: 6px;

									.title {
										vertical-align: middle;
										font-weight: 700;
										line-height: 1.1em;
										margin-bottom: 0.25em;
										text-transform: uppercase;
										color: #202020;
										overflow: hidden;
										font-size: 16px;

										@media #{$md-min} {
											font-size: 18px;
										}
										@media #{$lg-min} {
											font-size: 20px;
										}
									}

									.length {
										font-style: oblique;
										font-size: 13px;

										@media #{$sm-min} {
											font-size: 14px;
										}

									}

									.desc {
										clear: both;
										padding-top: 12px;
									}
								}

								.btn-container {
									position: absolute;
									width: 100%;
									left: 0;
									bottom: 0;
									padding: 15px;
									@media #{$xs-max} {
										padding-left: 57px;
									}

									.btn {
										width: 100%;
										font-size: 13px;
										background-color: transparent;
										border: 1px solid #007DB3;
										color: #007DB3;

										@media #{$sm-min} {
											&:hover {
												color: #FFFFFF;
												border: 1px solid #007DB3;
												background-color: #007DB3;
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

